// Page 404

.notfound {
	position: relative;
	display: flex;
	flex-direction: column;
	align-items: center;
	width: 100%;
	min-height: 100vh;
	padding: 7rem 0;
	background-color: #222;
	background-repeat: no-repeat;
	background-size: cover;
	background-position: top center;
	@include sm(padding, 5rem 0);
	&::before {
		content: "";
		position: absolute;
		top: 0;
		left: 0;
		bottom: 0;
		right: 0;
		background-color: rgba(0, 0, 0, 0.4);
		z-index: 1; }
	&__logo {
		position: relative;
		width: 16rem;
		height: auto;
		z-index: 10;
		@include sm(width, 14rem);
		@include sm(margin-bottom, 3rem); }
	&__content {
		position: relative;
		z-index: 10;
		max-width: 750px;
		margin: auto;
		padding: 0 15px;
		font-family: $titleFont;
		text-align: center; }
	&__text-large {
		font-size: 16em;
		font-weight: bold;
		line-height: 1;
		color: $accent;
		@include sm(font-size, 10em);
		@include xs(font-size, 8em); }
	&__text {
		margin-top: 2.5rem;
		margin-bottom: 5.5rem;
		font-size: 4.2em;
		line-height: 1.3;
		color: #fff;
		@include sm(font-size, 3.2em);
		@include xs(font-size, 2.8em);
		@include xs(margin-top, 2rem);
		@include xs(margin-bottom, 4rem); } }
