// Section Description

.s-description {
	padding-top: 8rem;
	padding-bottom: 8rem;
	@include sm(padding-top, 6rem);
	@include sm(padding-bottom, 4rem); }

.description {
	@include row-flex();
	&__img-block {
		@include col();
		@include size(5);
		@include size-sm(12); }
	&__text-block {
		@include col();
		@include size(7);
		@include size-sm(12);
		@include sm(margin-top, 3rem); }
	&__h2-bottom {
		display: block;
		margin-top: 1.6rem;
		font-family: $defaultFont;
		font-weight: normal;
		font-size: 1.6rem;
		font-style: italic;
		color: $themeDark;
		@include xs(font-size, 1.4rem);
		@include xs(margin-top, 0.5rem); }
	&__subtitle {
		margin-top: 4.8rem;
		margin-bottom: 1.8rem;
		@include xs(margin-top, 2.5rem); }
	&__subtext {
		margin-top: 1.8rem;
		margin-bottom: 4rem;
		@include xs(margin-top, 1rem);
		@include xs(margin-bottom, 2rem); }
	&__button {
		margin-top: 4rem;
		@include xs(margin-top, 3rem); } }
