// footer

.footer-top {
	min-height: 35rem;
	padding-top: 5.3rem;
	padding-bottom: 5.8rem;
	background-color: #29292a;
	@include sm(padding, 4rem 0); }

.footer-content {
	@include row-flex();
	&__item {
		@include col();
		@include size(3);
		@include size-md(4);
		@include size-sm(12);
		@include sm(margin-bottom, 3rem);
		&:nth-child(3) {
			@include size(2);
			@include size-md(4);
			@include size-sm(12); }
		&:nth-child(4) {
			@include size(4);
			@include size-md(8);
			@include size-sm(12);
			@include md(margin-top, 3rem);
			@include sm(margin, 0 15px); } }
	&__title-blog {
		margin-top: 3rem;
		margin-bottom: 1.6rem;
		font-weight: 600;
		line-height: 1.1;
		@include xs(line-height, 1.4);
		@include xs(font-size, 1.6rem); }
	&__link-blog {
		color: #fff;
		&:hover,
		&:focus {
			color: $accent; } }
	&__text-blog {
		font-size: 15px;
		line-height: 1.33;
		color: #727272; } }

.footer-contact {
	margin-top: 1.6rem;
	margin-bottom: 1.7rem;
	font-family: $titleFont;
	font-size: 1.5rem;
	font-weight: 600;
	color: #727272;
	&__icon-loc {
		float: left;
		font-size: 2rem;
		color: #9e9e9e;
		margin-right: 1.4rem;
		@include sm(float, none); }
	&__icon-phone {
		@extend .footer-contact__icon-loc;
		margin-right: 5px; }
	&__phone {
		@include clearfix();
		margin-top: 7px;
		& > a {
			@extend .footer-contact;
			margin: 0;
			&:hover,
			&:focus {
				color: $accent; } } } }

.footer-menu {
	margin-top: 3rem;
	&__item {
		margin-bottom: 1.2rem; }
	&__link {
		font: {
			family: $titleFont;
			weight: 600;
			size: 1.6em; }
		line-height: 1;
		text-transform: capitalize;
		color: #ffffff;
		&:hover,
		&:focus {
			color: $accent; } } }

.active {
	& > .footer-menu__link {
		color: $accent; } }

.instagram {
	@include u-row-flex(1rem);
	margin-top: 3rem;
	&__item {
		@include u-col(1rem);
		@include u-size(4, 12, 1rem);
		@include size-lg(6);
		@include size-md(4);
		@include size-xs(6);
		margin-bottom: 1rem;
		overflow: hidden; }
	&__link {
		display: block;
		backface-visibility: hidden;
		&:hover,
		&:focus {
			@include trf-s(1.1); } }
	&__img {
		display: block; } }

.footer-bottom {
	min-height: 6rem;
	background-color: #1f1f21; }

.copy {
	padding: 1.7rem 0;
	font-family: $titleFont;
	font-size: 1.7em;
	font-weight: normal;
	text-align: center;
	color: #727272;
	@include xs(font-size, 1.5em);
	&__link {
		color: $accent;
		&:hover,
		&:focus {
			color: #fff; } } }
