.s-facts {
	position: relative;
	min-height: 20rem;
	background-size: cover;
	background-position: center;
	background-repeat: no-repeat;
	&::before {
		content: "";
		position: absolute;
		top: 0;
		left: 0;
		bottom: 0;
		right: 0;
		background-color: rgba(#101010, 0.7); } }

.facts-wrap {
	@include row-flex();
	justify-content: center;
	padding: 7.3rem 0;
	opacity: 0.99;
	@include sm(padding, 4rem 0 0 0);
	@include xs(padding, 4rem 0 1rem 0); }

.facts-block {
	@include col();
	@include size(3);
	@include size-sm(12);
	@include sm(margin-bottom, 4rem);
	@include sm(text-align, center);
	@include xs(margin-bottom, 3rem);
	font-family: $titleFont;
	line-height: 1;
	color: #ffffff;
	&__count {
		font-size: 5.2em;
		font-weight: 600;
		@include xs(font-size, 3.8em); }
	&__text {
		display: inline-block;
		margin-left: 1.5rem;
		font-size: 2em;
		font-weight: bold;
		text-transform: uppercase;
		@include md(display, block);
		@include md(margin-left, 0);
		@include md(margin-top, 1.5rem);
		@include xs(font-size, 1.6em);
		@include xs(line-height, 1.2);
		@include xs(margin-top, 1rem); } }

@supports (align-content: center) {
	.facts-wrap {
		min-height: 20rem;
		align-content: center;
		padding: 0; } }
